.chess-square {
    width:var(--square-size);
    height:var(--square-size);
}

.chess-piece {
    width:var(--piece-size);
    height:var(--piece-size);
    background-size: var(--piece-size);
}

.chess-piece:hover {
    cursor:grab;
}

.chess-piece:active {
    cursor:grabbing;
}

